.App {
  text-align: center;
  background-color:#282c34;
  display: flex;
  flex-direction: column;
  padding: 0px 15px 0px 15px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

p {
  padding: 10px;
  /* margin:2px */
}

.B1 {
  background:
  linear-gradient(135deg, #434aaa 25%, transparent 25%) -25px 0,
  linear-gradient(225deg, #ff6d19 25%, transparent 25%) -25px 0,
  linear-gradient(315deg, #ff0c0c 25%, transparent 25%) 35px 0,
  linear-gradient(45deg, #efff0c 25%, transparent 25%) 15px 0;
  background-size: 100px 100px;
  /* background-color: #282c34;   */
   background-color: #2958b6;
  border-radius: 35px;
}

.B2 {
  display: flex;
  padding: 0px 40px;
  flex-direction: row;
  flex-wrap: wrap;
  background:
  linear-gradient(135deg, #434aaa 25%, transparent 25%) -25px 0,
  linear-gradient(225deg, #ff6d19 25%, transparent 25%) -25px 0,
  linear-gradient(315deg, #ff0c0c 25%, transparent 25%) 35px 0,
  linear-gradient(45deg, #efff0c 25%, transparent 25%) 15px 0;
  background-size: 10px 100px;
  /* background-color: #8d666d; */
  border-radius: 35px;
  justify-content: space-around;
}

.B2 > p {
  /* flex:1; */
  width:22vw;
  padding:25px;
  font-size: 1.2em;
  font-weight: 500;
  color:#ffffff;
  text-shadow:.2px .1px 15px #000000, 0 0 25px rgb(37, 0, 204), 0 0 9px #999;
  /* margin: 10px */
  box-shadow: rgba(0, 0, 0, 0.5) 20px 20px 50px;
  backdrop-filter: blur(6px);
  border-radius: 35px;
  margin:20px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
}
.p {
  box-shadow: rgba(0, 0, 0, 0.5) 20px 20px 50px;
  backdrop-filter: blur(5px);
  border-radius: 35px;
  margin:20px
}
.container {
  border-radius: 10px;
}

/* Letters have the text in them */
.BoxA {
  /* flex: 1; */
  background: rgb(34,193,195);
  background: linear-gradient(126deg, rgba(34,193,195,1) 5%, rgba(45,253,58,1) 100%);
  /* border-bottom: 5px solid red; */
  border-radius: 0px 0px 0px 35px;
}

.BoxB {
    /* flex: 1; */
    background: rgb(131,58,180);
background: linear-gradient(129deg, rgba(252,176,69,1) 10%, rgba(253,29,29,1) 50%,   rgba(131,58,180,1) 100%);
    /* border-bottom: 5px solid red; */
    border-radius: 0px 0px 0px 35px;
}

/* Numbers are the backdrop */
.Box1 {
  background-color: rgba(252,176,69,1);
  
  padding: none
}

.Box2 {
  /* flex: 1; */
background: rgb(34,193,195);
  /* background: linear-gradient(40deg, rgba(44,203,235,1) 0%, rgba(16,152,176,1) 39%, rgba(11,145,172,1) 66%, rgba(5,164,230,1) 100%); */
}

.container > :last-child {
  border-radius: 0px 0px 35px 35px ;
}

.container > :last-child > :last-child {
  border-radius: 0px 0px 35px 35px ;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Back {
  background:
  radial-gradient(circle at 100% 50%, transparent 20%, #43d808 21%,
    #2e9107 34%, transparent 35%, transparent),
  radial-gradient(circle at 0% 50%, transparent 20%, #2e9107 21%, #43d808 34%, transparent 35%, transparent) 0 -50px;
background-color: #35a735;
background-size: 75px 100px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
