body {
  margin: 0;
  background:
  linear-gradient(135deg, #efff0c 25%, transparent 25%) -50px 0,
  linear-gradient(225deg, #ff6d19 25%, transparent 25%) -50px 0,
  linear-gradient(315deg, #ff0c0c 25%, transparent 25%),
  linear-gradient(45deg, #efff0c 25%, transparent 25%);
  background-size: 100px 100px;
  background-color: #d40326;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
